import { Sniff } from "../utils";

class RO {
  constructor() {
    this.train = new Set();
    this.listen();
  }

  add({ name, update }) {
    this.train.add({ name, update });
  }

  listen() {
    const event = Sniff.mobile ? "orientationchange" : "resize";
    window.addEventListener(event, () => {
      const bounds = { vw: window.innerWidth, vh: window.innerHeight };
      this.train.forEach((o) => {
        o.update(bounds);
      });
    });
  }
}

const ro = new RO();
export { ro };
