import { Observer } from "../utils";
import { ticker } from "../core";

export class ObserverEffect {
  constructor(options = {}) {
    this.once = options.once || true;
    this.ismobile = options.ismobile || false;
    this.limit = options.limit || 10;
    this.observers = {};
    this.control = true;

    this.toVisible = [];
    this.toHidden = [];
    this.lock = false;

    this.update = this.update.bind(this);

    this.init();
  }

  init() {
    this.splitter();
    this.create({
      name: "lines",
      wrapperSelector: ".l-capture",
      targetSelector: ".l-line",
      visibleClass: "l-visible",
    });
    // this.create({
    //   name: "hr",
    //   wrapperSelector: "hr",
    //   visibleClass: "hr-visible",
    // });

    ticker.add({
      name: "reveal",
      update: this.update,
    });
  }

  splitter() {
    const paras = document.querySelectorAll(".l-capture");
    if (paras.length === 0) return;

    const canvasEl = document.createElement("canvas");
    const ghost =
      "OffscreenCanvas" in window
        ? canvasEl.transferControlToOffscreen()
        : canvasEl;
    const context = ghost.getContext("2d");

    paras.forEach((para) => {
      // console.log(para);
      const content = para.querySelector(".l-content");
      let words = content.textContent.split(" ");

      const lines = [];
      let curline = [];

      const compStyle = getComputedStyle(para);
      // const sz = compStyle.getPropertyValue("font-size");
      // const fm = compStyle.getPropertyValue("font-family");
      // const fs = compStyle.getPropertyValue("font-style");

      // context.font = `${fs} ${sz} ${fm}`;
      context.font = compStyle.getPropertyValue("font");
      if (compStyle.getPropertyValue("text-transform") === "uppercase") {
        words = words.map((word) => word.toUpperCase());
      }

      const maxwidth =
        parseFloat(compStyle.getPropertyValue("width")) -
        parseFloat(compStyle.getPropertyValue("padding-left")) -
        parseFloat(compStyle.getPropertyValue("padding-right"));

      const num_words = words.length;
      for (let i = 0; i < num_words; i++) {
        let curword = words[i].trim();
        if (curword.length === 0) continue;
        curline.push(curword);
        const line = curline.join(" ");
        if (context.measureText(line).width > maxwidth) {
          // break when line length extends
          const cache = curline.pop();
          lines.push(curline.join(" "));
          curline = [cache];
        }
      }
      lines.push(curline.join(" "));
      para.innerHTML = null;
      const paraFrag = document.createDocumentFragment();
      lines.forEach((line) => {
        const wrapperEl = document.createElement("span");
        wrapperEl.classList.add("l-wrapper");

        const lineEl = document.createElement("span");
        lineEl.classList.add("l-line");
        lineEl.innerText = line;

        wrapperEl.appendChild(lineEl);
        paraFrag.appendChild(wrapperEl);
      });
      para.appendChild(paraFrag);
      para.style.opacity = 1;
      return lines;
    });
  }

  sync(control) {
    this.control = control;
  }

  onEntry() {
    this.toVisible.forEach((node) => {
      node.el.classList.add(node.visibleClass);
      setTimeout(() => {
        node.el.classList.remove("l-line");
      }, 1500);
    });
    this.toVisible = [];
  }

  onExit() {
    this.toHidden.forEach((node) => {
      node.el.classList.remove(node.visibleClass);
    });
    this.toHidden = [];
  }

  update() {
    this.toVisible.length !== 0 && this.onEntry();
    this.toHidden.length !== 0 && this.onExit();
  }

  create({ name, wrapperSelector, targetSelector, visibleClass }) {
    const cache = {};

    const callback = (node, isIntersecting, unobserve) => {
      if (!this.control) return;
      let els = cache[node.dataset.revealId];
      let delay = parseFloat(node.dataset.revealDelay) || 75;
      if (isIntersecting) {
        if (this.once) {
          unobserve(node);
        }
        els.forEach((el, i) => {
          setTimeout(() => {
            this.toVisible.push({
              el,
              visibleClass,
            });
          }, delay * i);
        });
      } else if (!this.once) {
        els.forEach((el) => {
          this.toHidden.push({
            el,
            visibleClass,
          });
        });
      }
    };

    const o = Observer().create({
      callback,
      offset: `-5px`,
      threshold: 0.9,
    });

    this.observers[name] = o;

    document.querySelectorAll(wrapperSelector).forEach((el, i) => {
      el.dataset.revealId = i;
      cache[i] = el.querySelectorAll(targetSelector);
      o.observe(el);
    });
  }

  destroy() {
    for (const observer in this.observer) {
      observer.disconnect();
    }
    this.observers = {};
    this.toVisible = [];
    this.toHidden = [];
  }
}
