export const ImageLoader = ({ src, update, finalUpdate }) => {
  let loaded = 0;
  const n = src.length;
  const running = new Array(n);
  src.forEach((el, i) => {
    let img = new Image();
    img.src = el.src;
    const promise = img
      .decode()
      .catch((err) => {
        console.warn(`[IMG LOADER:]`, err);
      })
      .finally(() => {
        update && update(el, i, (++loaded / n) * 100);
        img = null;
      });
    running.push(promise);
  });
  Promise.all(running).then(() => {
    finalUpdate && finalUpdate();
  });
};

