/*

const throttledCallback = new Throttle({
    delay: 200,
    onlyAtEnd: true,
    cb: callback
})
throttledCallback.run()

*/

export class Throttle {
  constructor(o) {
    this.del = o.delay;
    this.onlyAtEnd = o.onlyAtEnd;
    this.cb = o.cb;
    this.last = 0;
    this.t = 0;
  }

  run() {
    let firstTime = true;
    const now = Performance.now();
    if ((this.last && now < this.last + this.del) || firstTime) {
      firstTime = false;
      clearTimeout(this.t);
      this.t = setTimeout(() => {
        this.last = now;
        this.cb();
      }, this.del);
    } else {
      this.last = now;
      if (!this.onlyAtEnd) {
        firstTime = false;
        this.cb();
      }
    }
  }
}
