import { ticker } from "../core/ticker";
import { bindAll } from "./BindAll";

/**
 *
 *
 * Utility to run callbacks after set delay
 *
 *
 */

let id = 0;
let delayTrain = [];
ticker.add({
  name: "delay",
  update: (delta) => {
    delayTrain.forEach((o) => o.update(delta));
  },
});

class Delay {
  constructor(update, duration) {
    this.update = update;
    this.duration = duration;
    this.elapsed = 0;
    this.paused = false;

    bindAll(this, ["run", "play", "pause", "destroy"]);

    this.init();
  }

  init() {
    this.id = id++;
    delayTrain.push({ id: this.id, update: this.run });
  }

  run(delta) {
    if (this.paused) return;
    this.elapsed += delta;
    if (this.elapsed >= this.duration) {
      this.update();
      this.destroy();
    }
  }

  play() {
    this.paused = false;
  }

  pause() {
    this.paused = true;
  }

  destroy() {
    delayTrain = delayTrain.filter((el) => el.id !== this.id);
    return null;
  }
}

export { Delay };
