import { Sniff, damp, clamp, round, bindAll, qs, qsa, bounds } from "../utils";
import { ticker, ro } from "../core";
import { Vau } from "../vau";

export class Scroller {
  constructor(opts) {
    this.settings = {
      inertia: 0.1,
      dragSpeed: 2,
      ...opts,
    };

    // convert inertia to log scale
    this.settings.inertia = Math.log(1 - this.settings.inertia / 30);

    this.docEl = document.scrollingElement || document.documentElement;

    this.bounds = {
      vh: window.innerHeight,
      vw: window.innerWidth,
    };

    window.scrollTo(0, 0);

    this.syncTrain = [];

    bindAll(this, ["resize", "update", "init"]);

    this.init();
    this.listeners();
    this.anchors();

    ticker.add({
      name: "scroller",
      update: this.update,
    });
    ro.add({
      name: "scroller",
      update: this.resize,
    });
  }

  init() {
    this.scrollContent = qs(`[data-scroll-content]`, this.docEl);
    this.scrollSections = qsa(`[data-scroll-section]`, this.scrollContent);
    this.scrollSections =
      this.scrollSections.length === 0
        ? [this.scrollContent]
        : this.scrollSections;

    this.instance = {
      cur: 0,
      target: 0,
      inertia: this.settings.inertia,
    };

    this.drag = {
      isDragging: false,
      snap: {
        x: 0,
        y: 0,
      },
      cur: {
        x: 0,
        y: 0,
      },
      lastScroll: 0,
    };

    this.resize();
    this.style();

    this.unlock();
    this.shouldAnimate = true;
    this.update();
  }

  get getScroll() {
    return window.pageYOffset;
  }

  listeners() {
    let deltaNorm = 1;
    Sniff.firefox && (deltaNorm *= 20);

    const setCurrent = (y) => {
      this.instance.cur = clamp(0, y, this.scrollHeight);
    };

    window.addEventListener(
      "wheel",
      (e) => {
        if (this.drag.isDragging || this.locked) return;
        setCurrent(this.instance.cur + e.deltaY * deltaNorm);
        this.shouldAnimate = true;
      },
      {
        passive: true,
      }
    );

    let shiftDir = 1;
    let spaceGap = this.bounds.vh - 40;
    document.addEventListener("keydown", (e) => {
      if (e.key === "ArrowDown") {
        setCurrent(this.instance.cur + 100);
      } else if (e.key === "ArrowUp") {
        setCurrent(this.instance.cur - 100);
      } else if (
        e.code == "Space" &&
        e.target.nodeName !== "INPUT" &&
        e.target.nodeName !== "TEXTAREA"
      ) {
        shiftDir = e.shiftKey ? -1 : 1;
        setCurrent(this.instance.cur + spaceGap * shiftDir);
      } else if (e.key === "PageDown") {
        setCurrent(this.instance.cur + this.bounds.vh);
      } else if (e.key === "PageUp") {
        setCurrent(this.instance.cur - this.bounds.vh);
      }
    });

    let start = "start";
    let end = "end";
    let pointer = "touch";
    let isTouch = true;

    if (!Sniff.touchDevice) {
      pointer = window.PointerEvent ? "pointer" : "mouse";
      start = "down";
      end = "up";
      isTouch = false;
    }

    window.addEventListener(
      `${pointer}${start}`,
      (e) => {
        this.drag.isDragging = true;
        this.drag.snap.y = isTouch ? e.changedTouches[0].clientY : e.clientY;
        this.drag.lastScroll = this.instance.cur;
      },
      {
        passive: true,
      }
    );

    window.addEventListener(
      `${pointer}move`,
      (e) => {
        if (!this.drag.isDragging) return;
        this.drag.cur.y = isTouch ? e.changedTouches[0].clientY : e.clientY;
        const diff =
          (this.drag.cur.y - this.drag.snap.y) * this.settings.dragSpeed;
        if (Math.abs(diff) < 15) return;
        setCurrent(this.drag.lastScroll - diff);
        this.shouldAnimate = true;
      },
      {
        passive: true,
      }
    );

    window.addEventListener(
      `${pointer}${end}`,
      (e) => {
        if (!this.drag.isDragging) return;
        this.drag.isDragging = false;
      },
      {
        passive: true,
      }
    );
  }

  scrollTo(y) {
    new Vau({
      duration: 750,
      easing: "io4",
      generic: [[this.instance.cur, y]],
      update: (ctx) => {
        this.instance.cur = ctx.generic[0];
      },
    });
  }

  anchors() {
    qs(`[data-scroll-top]`).addEventListener("click", () => {
      this.scrollTo(0);
    });
    qsa(`[data-scroll-to]`).forEach((el) => {
      const target = bounds(qs(el.dataset.scrollTo)).top - 25;
      el.addEventListener("click", () => {
        this.scrollTo(target);
      });
    });
  }

  style() {
    this.scrollContent.style = `width: 100vw; min-height: 100vh; position:fixed;`;
    // this.scrollSections.forEach((section) => {
    //   section.style.contain = "content";
    // });
  }

  resize(o = { vw: window.innerWidth, vh: window.innerHeight }) {
    this.bounds = o;

    this.scrollHeight =
      Math.max(this.scrollContent.scrollHeight, this.bounds.vh) -
      this.bounds.vh;
    document.body.style.overscrollBehavior = `none`;

    this.cache = [];
    for (const section of this.scrollSections) {
      const top = section.offsetTop;

      let speed = 1;
      if (section.dataset.scrollSpeed !== undefined) {
        speed = parseFloat(section.dataset.scrollSpeed);
      }

      this.cache.push({
        visible: true,
        top: top - this.bounds.vh,
        bottom: top + section.scrollHeight,
      });
    }

    this.shouldAnimate = true;
    this.update();
  }

  update(delta = 1000 / 60) {
    if ((!this.shouldAnimate && this.animComplete) || this.locked) return;

    this.instance.target = damp(
      this.instance.target,
      this.instance.cur,
      this.instance.inertia,
      delta
    );

    this.scrollSections.forEach((section, i) => {
      const el = this.cache[i];
      let y = this.instance.target;
      if (y > el.top && y <= el.bottom) {
        if (!el.visible) {
          el.visible = true;
          section.style.pointerEvents = "all";
          section.style.opacity = 1;
        }
        section.style.transform = `translate3d(0, ${-y}px, 0)`;
      } else if (el.visible) {
        section.style.pointerEvents = "none";
        section.style.opacity = 0;
        section.style.transform = "none";
        el.visible = false;
      }
    });

    for (const update of this.syncTrain) {
      update(this.instance.target);
    }

    this.shouldAnimate = false;
  }

  get animComplete() {
    return round(this.instance.cur) === round(this.instance.target);
  }

  sync(update) {
    this.syncTrain.push(update);
  }

  lock() {
    this.locked = true;
    // lock position too?
  }

  unlock() {
    this.locked = false;
  }
}
