import { Sniff } from "../utils/Sniff";
import { bindAll } from "../utils/BindAll";

/**
 *
 * Global ticker to reduce
 * memory overhead
 *
 * TODO:
 * Update to use Array instead of Set
 */

class Ticker {
  constructor() {
    this.train = new Set();
    this.once = [];

    this.instance = {
      lastFrameTime: 0,
      nextFrame: null,
      targetFPS: 60,
    };

    this.targetDelta = 1000 / this.instance.targetFPS;
    this.maxDelta = this.targetDelta * 2;
    this.listeners();

    bindAll(this, ["run", "play", "pause"]);

    this.paused = true;
  }

  add({ name, update, begin, end }) {
    this.train.add({
      name,
      update,
      begin,
      end,
    });
    return this.ref(name);
  }

  nextFrame(update) {
    this.once.push(update);
  }

  remove(name) {
    for (const o of this.train) {
      if (o.name === name) {
        return this.train.delete(o);
      }
    }
  }

  ref(name) {
    for (const o of this.train) {
      if (o.name === name) {
        return o.animation;
      }
    }
    return false;
  }

  run(elapsedTime = 0) {
    let delta = elapsedTime - (this.instance.lastFrameTime || 0);
    this.instance.nextFrame = requestAnimationFrame(this.run);
    this.instance.lastFrameTime = elapsedTime;

    delta = Math.min(delta, this.targetDelta);

    for (const o of this.train) {
      o.begin && o.begin();
      o.update(delta, elapsedTime);
      o.end && o.end();
    }

    for (const update of this.once) {
      update(delta, elapsedTime);
    }

    this.once = [];
  }

  pause() {
    if (this.paused) return;
    cancelAnimationFrame(this.instance.nextFrame);
    this.paused = true;
  }

  play() {
    if (!this.paused) return;
    this.paused = false;
    this.instance.lastFrameTime = performance.now();
    requestAnimationFrame(this.run);
  }

  listeners() {
    document.addEventListener("visibilitychange", (e) => {
      document.visibilityState === "hidden" ? this.pause() : this.play();
    });

    !Sniff.safari &&
      window.addEventListener("pagehide", (e) => {
        this.pause();
      });
  }
}

// Global ticker
const ticker = new Ticker();
ticker.play();
export { ticker };
