import { ObserverEffect, Scroller, TableHover } from "./animations";
import { Sniff } from "./utils";
import { Vau } from "./vau";

window.scrollTo(0, 0);

window.addEventListener("load", () => {
  let s, t;

  const o = new ObserverEffect();

  if (!Sniff.mobile) {
    t = new TableHover();
    s = new Scroller({
      inertia: 0.125,
    });
    s.lock();
  }

  new Vau({
    targets: "#loader",
    opacity: [1, 0],
    easing: "i4",
    duration: 1250,
    delay: Sniff.mobile ? 0 : 1500,
    complete: () => {
      document.getElementById("loader").remove();
      !Sniff.mobile && s.unlock();
    },
  });
});

const palette = {
  blue: `rgb(28,180,228)`,
  orange: `rgb(252,100,4)`,
  light: `rgb(230, 230, 230)`,
};
