import { lerp, clamp, Ease, qsa, isArray, bindAll, Delay } from "../utils";
import { ticker } from "../core";

/**
 *
 * TODO:
 *
 * 1) Decouple timer/progress and
 * DOM update callbacks
 *
 * 3) Add reverse direction
 *
 * 5) Unset transform on complete?
 */

/**
 *
 * Target API:
 *
 * new Vau({
 *   targets: [el],
 *   easing: "io3",
 *   duration: 1000,
 *   transform: {
 *     tx: [0, 100, "%"], // from, to, unit
 *     ty: 300, // take default 0 + px, else from previous animation in timeline
 *     sx: [1, 2], // scale
 *     rz: 90, // rotate3d, default unit - degrees
 *   },
 *   opacity: [1, 1],
 *   reverse: true
 * });
 *
 *
 */

// -------------------------------------------------------------
// -------------------------------------------------------------
// -------------------------------------------------------------
// -------------------------------------------------------------

/**
 *
 *
 * Global animation train
 *
 *
 */
let id = 0;
let vauTrain = [];
ticker.add({
  name: "vau",
  update: (delta) => {
    vauTrain.forEach((o) => o.update(delta));
  },
});

// convert into matrix?
const createTransform = ({
  tx = null, // [val, unit (px or %) ]
  ty = null, // [val, unit (px or %) ]
  sx = null, // val (0-1)
  sy = null, // val (0-1)
  rx = null, // val (deg)
  ry = null, // val (deg)
  rz = null, // val (deg)
}) => {
  let t = ``;
  if (tx || ty || tx === 0 || ty === 0) {
    !tx && tx !== 0 && (tx = [0, "px"]);
    !ty && ty !== 0 && (ty = [0, "px"]);
    t += `translate3d(${tx[0]}${tx[1]}, ${ty[0]}${ty[1]}, 0) `;
  }
  if (sx || sy || sx === 0 || sy === 0) {
    !sx && sx !== 0 && (sx = 1);
    !sy && sy !== 0 && (sy = 1);
    t += `scale3d(${sx}, ${sy}, 1) `;
  }
  if (rx || ry || rz || rx === 0 || ry === 0 || rz === 0) {
    !rx && rx !== 0 && (rx = 0);
    !ry && ry !== 0 && (ry = 0);
    !rz && rz !== 0 && (rz = 0);
    t += `rotate3d(${rx}, ${ry}, ${rz}, ${rx + ry + rz}deg)`;
  }
  // console.log(t);
  return t;
};

const transform = (el, props, units) => {
  props.tx = [props.tx, units[0]];
  props.ty = [props.ty, units[1]];
  el.style.transform = createTransform(props);
};

/**
 *
 *
 * Vau animations
 *
 *
 */

class Vau {
  constructor({
    targets = null,
    generic = null,
    transform = null,
    opacity = null,
    duration = 0,
    delay = 0,
    easing = "linear",
    begin = null,
    complete = null,
    update = null,
    autoplay = true,
  }) {
    // check if targets exist
    let t;
    if (typeof targets === typeof "") {
      t = qsa(targets);
    } else if (targets && isArray(targets)) {
      t = targets;
    } else {
      t = false;
    }

    // default values for update flags
    this.updateOpacity = false;
    this.updateTransform = false;

    if (t) {
      // check if opacity needs updates
      opacity && (this.updateOpacity = true);

      // check if transform needs updates
      transform && (this.updateTransform = true);
    }

    this.callbacks = {
      begin,
      complete,
      update,
    };

    this.config = {
      duration,
      delay,
      easing: Ease[easing],
    };

    this.state = {
      targets: t,
      elapsed: 0,
      easeProgress: 0,
      dom: {
        transform: {},
        opacity: [],
      },
      generic: [],
    };

    let txs = Object.assign(
      {
        tx: null,
        ty: null,
        sx: null,
        sy: null,
        rx: null,
        ry: null,
        rz: null,
      },
      transform
    );

    if (this.updateTransform) {
      !txs.tx && (txs.tx = ["", "", "px"]);
      !txs.ty && (txs.ty = ["", "", "px"]);
      txs.tx && !txs.tx[2] && (txs.tx[2] = "px");
      txs.ty && !txs.ty[2] && (txs.ty[2] = "px");
    }

    let nco = this.updateOpacity && isArray(opacity) ? opacity : [1, opacity];

    // internal props array for tweening
    this.DOMprops = {
      transform: txs,
      opacity: nco,
    };
    this.genericProps = generic ? generic : [];

    // set current values to initial values
    // if (this.updateTransform) {
    //   for (const name in txs) {
    //     const el = txs[name];
    //     el && (this.state.dom.transform[name] = el[0]);
    //   }
    // }
    // this.updateOpacity && (this.state.dom.opacity = nco[0]);

    // this.state.generic = this.genericProps.map((el) => el[0]);

    this.progress = 0;
    this.paused = !autoplay;

    bindAll(this, ["update", "destroy", "pause", "play", "restart"]);

    this.init();

    txs = null;
    nco = null;
  }

  init() {
    this.id = id++;
    this.delay = new Delay(() => {
      vauTrain.push({ id: this.id, update: this.update });
      this.callbacks.begin && this.callbacks.begin(this.state);
    }, this.config.delay);
  }

  // pass in delta time since last frame
  update(t = 1000 / 60) {
    // check play status
    if (this.paused) return;

    // final update
    if (this.state.elapsed > this.config.duration) {
      this.destroy();
      this.callbacks.complete && this.callbacks.complete();
      return;
    }

    this.state.elapsed += t;
    this.progress = clamp(this.state.elapsed / this.config.duration, 0, 1);
    this.state.easeProgress = this.config.easing(this.progress);

    /**
     *
     *
     * TODO: Combine lerps into one loop
     *
     */

    //  update transform
    if (this.updateTransform) {
      for (const propName in this.DOMprops.transform) {
        const val = this.DOMprops.transform[propName];
        if (val) {
          this.state.dom.transform[propName] = lerp(
            val[0],
            val[1],
            this.state.easeProgress
          );
        }
      }
    }

    // update opacity
    if (this.updateOpacity) {
      this.state.dom.opacity = lerp(
        this.DOMprops.opacity[0],
        this.DOMprops.opacity[1],
        this.state.easeProgress
      );
    }

    // update generic
    let i = 0;
    for (const prop of this.genericProps) {
      this.state.generic[i++] = lerp(prop[0], prop[1], this.state.easeProgress);
    }

    this.callbacks.update && this.callbacks.update(this.state);
    this.state.targets && this.setDOM(this.state.dom);
  }

  setDOM(props) {
    for (const target of this.state.targets) {
      this.updateTransform &&
        transform(target, props.transform, [
          this.DOMprops.transform.tx[2],
          this.DOMprops.transform.ty[2],
        ]);
      this.updateOpacity &&
        props.opacity >= 0 &&
        (target.style.opacity = props.opacity);
    }
  }

  pause() {
    this.paused = true;
    this.delay && this.delay.pause();
  }

  play() {
    this.paused = false;
    this.delay && this.delay.play();
  }

  restart() {
    this.pause();
    this.destroy();

    this.state.elapsed = 0;

    this.init();
    this.play();
  }

  destroy() {
    vauTrain = vauTrain.filter((el) => el.id !== this.id);
    this.delay && (this.delay = this.delay.destroy());
  }
}

/**
 *
 *
 * Timeline Animations
 *
 *
 */

class Timeline {
  constructor(o) {
    this.train = [];
    this.delay = 0;
    this.defaults = o;
    bindAll(this, ["play", "pause", "restart"]);
  }

  add(o) {
    o = {
      ...this.defaults,
      ...o,
    };
    // combine delays to create timeline
    this.delay += o.delay || 0;
    o.delay = this.delay;
    this.train.push(new Vau(o));
    this.delay += o.duration || 0;
    return this;
  }
  play() {
    this.train.forEach((o) => o.play());
  }
  pause() {
    this.train.forEach((o) => o.pause());
  }
  restart() {
    this.train.forEach((o) => o.restart());
  }
}

export { Vau, Timeline };
