import { Delay } from "../utils";

export class TableHover {
  constructor() {
    this.table = document.getElementById("table-container");
    this.rows = document.querySelectorAll(".hover-line");
    this.activeRow = null;
    this.currentDelay = null;
    this.listen();
  }

  enterStyle() {
    this.currentDelay = new Delay(() => {
      this.table.querySelectorAll(".inactive").forEach((el) => {
        el.style.opacity = 0;
      });
    }, 200);
  }

  leaveStyle() {
    this.currentDelay.destroy();
    this.rows.forEach((row) => {
      row.style.opacity = 1;
    });
  }

  listen() {
    this.rows.forEach((row) => {
      row.classList.add("inactive");
      row.addEventListener("pointerenter", () => {
        row.classList.remove("inactive");
        this.activeRow = row;
        this.enterStyle();
      });
      row.addEventListener("pointerleave", () => {
        row.classList.add("inactive");
        this.leaveStyle();
      });
    });
  }
}
